<template>
<div class="w-100">
    <div class="col my-page-view-complete">
        <ViewComplete :statusComplete="statusComplete" :filter="filterComplete" />
    </div>
    <div class="contact" v-if="contacts">
        <template v-for="(contact, index) in contacts">
            <ContactItem :contact="contact" :key="index" :type="'contact'" />
        </template>
        <InfiniteLoading :isError="isError" :isComplete="isComplete" :fetchData="fetchContact" :classParent="'.contact'" />
    </div>
    <div class="contact" v-if="isUpdateContact && contacts && !contacts.length">
        <div class="no-results" slot="no-results">{{$t('Common.Loading.NoResult')}}</div>
    </div>
</div>
</template>

<script>
import ContactItem from '@/components/MyPage/ContactItem'
import ViewComplete from '@/components/Common/Button/ViewComplete'
import InfiniteLoading from '@/components/Common/Loading/InfiniteLoading'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_CONTACT_LIST,
    CREATE_CONTACT_LIST,
    RESET_CONTACT_LIST
} from '@/mutations/MyPage.mutation'
import {
    PROJECT_STATUS,
} from '@/constants'

export default {
    name: 'ContactList',
    components: {
        ContactItem,
        ViewComplete,
        InfiniteLoading
    },
    data: function () {
        return {
            page: 0,
            isError: false,
            isComplete: false,
            statusComplete: false,
            user: JSON.parse(localStorage.getItem('user')),
            isUpdateContact: null
        }
    },
    computed: {
        contacts() {
            let apls = this.$store.state.MyPage.contacts;
            if (this.statusComplete) {
                return apls.filter(apl => apl.projStatus == PROJECT_STATUS.COMPLETE)
            }
            return apls;
        },
        totalContact() {
            return this.$store.state.MyPage.totalContact;
        }
    },
    async created() {
        await this.$store.dispatch("CREATE_CONTACT_LIST");
    },
    methods: {
        async fetchContact() {
            try {
                this.page += 1;
                let params = {
                    pageNum: this.page + ''
                }

                if (this.user) {
                    params['userId'] = this.user.userId
                }
                await this.$store.dispatch("FETCH_CONTACT_LIST", params);
                this.isUpdateContact = true;

                if (this.totalContact == 0 || this.contacts && this.contacts.length == this.totalContact) {
                    this.isComplete = true;
                }
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
                this.isError = true;
            }
        },

        resetData() {
            this.$store.dispatch('RESET_CONTACT_LIST')
            this.page = 0;
            this.isComplete = false;
            this.isUpdateContact = false;
            this.isError = false;
        },
        filterComplete() {
            this.statusComplete = !this.statusComplete
        }
    },
    beforeDestroy() {
        this.resetData();
    }
}
</script>

<style lang="scss" scoped>
.contact {
    max-height: calc(100vh - 17rem);
    overflow: auto;
}
</style>
