<template>
<div class="col-12 p-0" v-if="myCareer">
    <template v-if="!myCareer.resumeFile">
        <div class="row my-career-row-content full-width">
            <FileInputFull isRequired :title="$t('Joining.UploadResume')" :description="$t('Joining.UploadDesc')" :wrapperClass="'w-100'" :placeholder="''" />
        </div>
    </template>
    <template v-else>
        <Gap :height="'1rem'" />
    </template>
    <div class="row my-career-row-content full-width">
        <PersonalInfoItem />
    </div>
    <Gap :height="'1rem'" />
    <div class="row my-career-row-content full-width">
        <BasicInfoItem />
    </div>
    <Gap :height="'1rem'" />
    <div class="row my-career-row-content full-width">
        <LangSkillsItem />
    </div>
    <template v-if=" myCareer.resumeFile">
        <Gap :height="'1rem'" />
        <div class="row my-career-row-content full-width">
            <ResumeItem :deleteResume="deleteResume" :updateResume="updateResume" :nameFile="myCareer.resumeRealFile" />
        </div>
    </template>
</div>
</template>

<script>
import FileInputFull from '@/components/Common/Input/FileInputFull'
import PersonalInfoItem from '@/components/MyPage/PersonalInfoItem'
import BasicInfoItem from '@/components/MyPage/BasicInfoItem'
import LangSkillsItem from '@/components/MyPage/LangSkillsItem'
import ResumeItem from '@/components/MyPage/ResumeItem'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_MY_CAREER
} from '@/mutations/MyPage.mutation'

export default {
    name: 'MyCareer',
    components: {
        FileInputFull,
        PersonalInfoItem,
        BasicInfoItem,
        LangSkillsItem,
        ResumeItem
    },
    data: function () {
        return {
            myCareer: null
        }
    },
    async created() {
        await this.fetchMyCareer();
    },
    methods: {
        async fetchMyCareer() {
            try {
                let user = JSON.parse(localStorage.getItem('user'))
                if (user) {
                    let params = {
                        userId: user.userId
                    }
                    await this.$store.dispatch("FETCH_MY_CAREER", params);
                    this.myCareer = this.$store.state.MyPage.myCareer;
                }
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
            }
        },
        async deleteResume() {

        },
        async updateResume() {

        },
    },
}
</script>
