<template>
<div class="mypage-info-item">
    <div class="container-fluid">
        <div class="row row-info-item">
            <span class="col title">{{$t('BasicInfo.Title')}}</span>
            <span class="col action edit" @click="gotoBasicPage"></span>
        </div>
        <div class="divider"></div>
        <div class="row row-info-item body">
            <div class="col-12 bg">
                <div class="col">
                    <div class="row">
                        <span class="col keys">{{$t('BasicInfo.IT_Experience')}}</span>
                        <span class="col values">{{personalInfo.IT_Experience}}</span>
                    </div>
                    <div class="row">
                        <span class="col keys">{{$t('BasicInfo.SAP_Experience')}}</span>
                        <span class="col values">{{personalInfo.SAP_Experience}}</span>
                    </div>
                </div>
            </div>
            <div class="col-12">
                <div class="row">
                    <span class="col keys">{{$t('BasicInfo.Status')}}</span>
                    <span class="col values"><span class="status-btn">{{personalInfo.userStatusLabel}}</span></span>
                </div>
                <div class="row">
                    <span class="col keys">{{$t('BasicInfo.Available_Date')}}</span>
                    <span class="col values">{{personalInfo.availDt}}</span>
                </div>
                <div class="row">
                    <span class="col keys">{{$t('BasicInfo.Job_1')}}</span>
                    <span class="col values">{{personalInfo.job2}}</span>
                </div>
                <div class="row">
                    <span class="col keys">{{$t('BasicInfo.Job_2')}}</span>
                    <span class="col values">{{personalInfo.job2}}</span>
                </div>
                <div class="row">
                    <span class="col keys">{{$t('BasicInfo.Place_1')}}</span>
                    <span class="col values">{{personalInfo.hopePlace1}}</span>
                </div>
                <div class="row">
                    <span class="col keys">{{$t('BasicInfo.Place_2')}}</span>
                    <span class="col values">{{personalInfo.hopePlace2}}</span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import moment from 'moment'
import {
    STATUS,
} from '@/constants'

export default {
    name: "basic-info-item",
    computed: {
        personalInfo() {
            return this.$store.state.MyPage.myCareer;
        },
    },
    async created() {
        if (this.personalInfo) {

            this.personalInfo['IT_Experience'] = this.convertTime(this.personalInfo.itCareerYear, this.personalInfo.itCareerMonth);
            this.personalInfo['SAP_Experience'] = this.convertTime(this.personalInfo.sapCareerYear, this.personalInfo.sapCareerMonth);
            this.personalInfo['hopePlace1'] = `${this.personalInfo.location11Nm}, ${this.personalInfo.location12Nm}, ${this.personalInfo.location13Nm}`;
            this.personalInfo['hopePlace2'] = `${this.personalInfo.location21Nm}, ${this.personalInfo.location22Nm}, ${this.personalInfo.location23Nm}`;
            this.personalInfo['job1'] = `${this.personalInfo.job11Nm} ${this.personalInfo.job12Nm}`;
            this.personalInfo['job2'] = `${this.personalInfo.job21Nm} ${this.personalInfo.job22Nm}`;
            this.personalInfo['job2'] = `${this.personalInfo.job21Nm} ${this.personalInfo.job22Nm}`;
            this.personalInfo.userStatusLabel = this.personalInfo.userStatus == STATUS.IN_PROGRESS ? this.$t('BasicInfo.InProgress') : this.$t('BasicInfo.WAITING');
            this.personalInfo.availDt = moment(this.personalInfo.availDt).format('YYYY.MM.DD');
        }
    },
    methods: {
        gotoBasicPage: function () {
            return this.$router.push('/basic-information');
        },
        convertTime(year, month) {
            let stringYear = +year + ' ' + (+year >= 1 ? this.$t("BasicInfo.Years") : this.$t("BasicInfo.Year")) + ' '
            let stringMonth = +month + ' ' + (+month >= 1 ? this.$t("BasicInfo.Months") : this.$t("BasicInfo.Month"))
            return stringYear + stringMonth
        }
    }
}
</script>

<style lang="scss" scoped>
.mypage-info-item {
    display: inline-flex;
    width: 100%;
    background-color: #FFFFFF;

    .row-info-item {
        padding-top: .8rem;
        padding-bottom: 1.0rem;

        &.body {
            padding-top: 0;
            padding-bottom: 1rem;

            .bg {
                background-color: rgba(38, 153, 251, 0.1);
            }
        }
    }
}

.col {
    &.keys {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 30px;
        display: flex;
        align-items: center;
        color: #4F4F4F;
        margin: 4.5px 0;
    }

    &.values {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 30px;
        display: flex;
        align-items: center;
        color: #4F4F4F;
        justify-content: flex-end;
        margin: 4.5px 0;
        word-break: keep-all;
        text-align: right;
    }

    &.title {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2.3rem;
        display: flex;
        align-items: center;
        color: #333333;
    }

    &.action {
        max-width: 3.5rem;
        padding-left: 0;
    }

    &.avatar {
        background-image: url(~@/assets/images/avatar-example.png);
        background-repeat: no-repeat;
        background-size: 75px 100px;
        background-position: right;
        height: 100px;
        max-width: 90px;
        padding-right: 0;
    }

    &.info {
        .name {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 23px;
            display: flex;
            align-items: center;
            color: #000000;
            margin-bottom: 6px;
        }

        .detail {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 22px;
            color: #4F4F4F;
            display: inline-flex;
            width: 100%;
        }
    }
}

.action {
    background-repeat: no-repeat;
    background-position: left;
    background-size: 2.0rem 2.0rem;

    &.edit {
        background-image: url(~@/assets/images/edit.png);
    }
}

.divider {
    height: 0.1rem;
    background-color: #E0E0E0;
}

.status-btn {
    background: rgba(38, 153, 251, 0.1);
    border-radius: 100px;
    padding: 0 10px;
    font-family: NotoSansKR;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 36px;
    display: flex;
    align-items: center;
    text-align: right;
    color: #006FE3;
    height: 27px;
}
</style>
