<template>
<div class="mypage-info-item">
    <div class="container-fluid" v-if="personalInfo">
        <div class="row row-info-item">
            <span class="col title">{{$t('Personal.PersonalInfo')}}</span>
            <span class="col action edit" @click="gotoPersonalPage"></span>
        </div>
        <div class="divider"></div>
        <div class="row row-info-item body">
            <div class="col avatar"></div>
            <div class="col info">
                <span class="name">{{personalInfo.userNm}}</span>
                <span class="detail">{{personalInfo.userTel | converTel}}</span>
                <span class="detail">{{personalInfo.userId}}</span>
                <span class="detail">{{personalInfo.countryNm}}, {{personalInfo.addr}}, {{personalInfo.addrDetail}}</span>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "mypage-info-item",
    computed: {
        personalInfo() {
            return this.$store.state.MyPage.myCareer;
        },
    },
    filters: {
        converTel(value) {
            var cleaned = ('' + value).replace(/\D/g, '')
            var match = cleaned.match(/^(\d{3})(\d{3})(\d{3,4})$/)
            if (match) {
                return  match[1] + '-' + match[2] + '-' + match[3]
            }
            return value
        }
    },
    methods: {
        gotoPersonalPage: function () {
            return this.$router.push('/personal-information');
        }
    }
}
</script>

<style lang="scss" scoped>
.mypage-info-item {
    display: inline-flex;
    width: 100%;
    background-color: #FFFFFF;

    .row-info-item {
        padding-top: .8rem;
        padding-bottom: 1.0rem;

        &.body {
            padding-top: 15px;
            padding-bottom: 15px;
        }
    }
}

.col {
    &.title {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2.3rem;
        display: flex;
        align-items: center;
        color: #333333;
    }

    &.action {
        max-width: 3.5rem;
        padding-left: 0;
    }

    &.avatar {
        background-image: url(~@/assets/images/avatar-example.png);
        background-repeat: no-repeat;
        background-size: 75px 100px;
        background-position: right;
        height: 100px;
        max-width: 90px;
        padding-right: 0;
    }

    &.info {
        .name {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 23px;
            display: flex;
            align-items: center;
            color: #000000;
            margin-bottom: 6px;
        }

        .detail {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 22px;
            color: #4F4F4F;
            display: inline-flex;
            width: 100%;
        }
    }
}

.action {
    background-repeat: no-repeat;
    background-position: left;
    background-size: 2.0rem 2.0rem;

    &.edit {
        background-image: url(~@/assets/images/edit.png);
    }
}

.divider {
    height: 0.05rem;
    background-color: #E0E0E0;
}
</style>
