<template>
<div class="contact-item-wrapper" v-if="contact">
    <div class="row m-0">
        <div class="col project-title">
            <span class="project-status" :class="contact.projectStatus">{{contact.projectStatus ? contact.projectStatus : ''}}</span><span class="project-name">{{contact.projNm ? contact.projNm : ''}}</span>
        </div>
    </div>
    <div class="row m-0">
        <div class="col project-content">
            <div class="row">
                <div class="label">{{$t('MyPage.Requirement')}}</div>
                <div class="col content">{{contact.job1Nm ? contact.job1Nm : ''}}</div>
            </div>
            <div class="row">
                <div class="label">{{$t('MyPage.TechnicalClassification')}}</div>
                <div class="col content">{{contact.job2Nm ? contact.job2Nm : ''}}</div>
            </div>
            <div class="row">
                <div class="label">{{$t('MyPage.RecruitingYear')}}</div>
                <div class="col content">{{contact.recruitYear}}</div>
            </div>
            <div class="row">
                <div class="label">{{$t('MyPage.ProgressPreriod')}}</div>
                <div class="col content">{{contact.preriod}}</div>
            </div>
            <div class="row">
                <div class="label">{{$t('MyPage.CurrentState')}}</div>
                <template v-if="this.contact && this.contact.appStatus && type == 'apply'">
                    <div class="col content">
                        <span class="contact-btn" :class="contact.applyStatus">
                            {{contact.applyStatus ? contact.applyStatus : ''}}
                        </span>
                    </div>
                </template>
                <template v-if="this.contact && this.contact.inqStatus && type == 'contact'">
                    <div class="col content">
                        <span class="contact-btn" :class="contact.contactStatus">
                            {{contact.contactStatus ? contact.contactStatus : ''}}
                        </span>
                    </div>
                </template>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    PROJECT_STATUS,
    CURRENT_STATUS
} from '@/constants'
import moment from 'moment'

export default {
    name: 'ContactItem',
    props: {
        contact: null,
        type: null
    },
    created() {
        if (this.contact && this.contact.projStatus) {
            switch (this.contact.projStatus) {
                case PROJECT_STATUS.PROCESS:
                    this.contact.projectStatus = this.$t('MyPage.PROJ_STATUS.PROCESS')
                    break;
                case PROJECT_STATUS.COMPLETE:
                    this.contact.projectStatus = this.$t('MyPage.PROJ_STATUS.COMPLETE')
                    break;
                default:
                    break;
            }
        }

        if (this.contact && this.contact.appStatus && this.type == 'apply') {
            switch (this.contact.appStatus) {
                case CURRENT_STATUS.PASS:
                    this.contact.applyStatus = this.$t('MyPage.CURRENT_STATE.PASS')
                    break;
                case CURRENT_STATUS.FAILED:
                    this.contact.applyStatus = this.$t('MyPage.CURRENT_STATE.FAILED')
                    break;
                case CURRENT_STATUS.APPLIED:
                    this.contact.applyStatus = this.$t('MyPage.CURRENT_STATE.APPLIED')
                    break;
                case CURRENT_STATUS.INTERVIEW_RQ:
                    this.contact.applyStatus = this.$t('MyPage.CURRENT_STATE.INTERVIEW_RQ')
                    break;
                default:
                    break;
            }
        }

        if (this.contact && this.contact.inqStatus && this.type == 'contact') {
            switch (this.contact.inqStatus) {
                case CURRENT_STATUS.WAITING:
                    this.contact.contactStatus = this.$t('MyPage.CURRENT_STATE.WAITING')
                    break;
                case CURRENT_STATUS.COMPLETE:
                    this.contact.contactStatus = this.$t('MyPage.CURRENT_STATE.COMPLETE')
                    break;
                default:
                    break;
            }
        }

        this.contact.recruitYear = this.contact.career ? (+this.contact.career + ' ' + (+this.contact.career > 1 ? this.$t('BasicInfo.Years') : this.$t('BasicInfo.Year'))) : '';
        if (this.contact.projFrom && this.contact.projTo) {
            this.contact.preriod = moment(this.contact.projFrom).format('YYYY.MM.DD') + '~' + moment(this.contact.projTo).format('YYYY.MM.DD')
        } else this.contact.preriod = ''
    }
}
</script>

<style lang="scss" scoped>
.contact-item-wrapper {
    width: 100%;
    background: #FFFFFF;

    &~.contact-item-wrapper {
        margin-top: 1.0rem;
    }

    .project-title {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2.3rem;
        display: flex;
        align-items: center;
        color: #333333;
        padding: 1rem 0rem;
        margin: 0rem 1.5rem;
        border-bottom: 0.05rem solid #E0E0E0;

        .project-status {
            background: #006FE3;
            border-radius: 2.0rem;
            padding-left: .7rem;
            padding-right: .7rem;
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.2rem;
            line-height: 2.0rem;
            align-items: center;
            color: #FFFFFF;
            margin-right: 1.0rem;
            display: inline-flex;

            &.Complete {
                background-color: #27AE60;
            }

            &.Watting {
                background-color: #F2994A;
            }

            &.Fail {
                background-color: #EB5757;
            }
        }
    }

    .project-content {
        padding-top: .8rem;
        padding-bottom: .8rem;

        .label {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 300;
            font-size: 1.4rem;
            line-height: 3.0rem;
            color: #4F4F4F;
            padding-left: 1.5rem;
        }

        .content {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.4rem;
            line-height: 3.0rem;
            text-align: right;
            color: #4F4F4F;
            padding-right: 1.5rem;

            .contact-btn {
                box-sizing: border-box;
                border-radius: 2px;
                font-family: NotoSansKR;
                font-style: normal;
                font-weight: 500;
                font-size: 12px;
                line-height: 17px;
                display: inline-flex;
                align-items: center;
                text-align: center;
                padding: 0 8px;
                border: 1px solid #F2994A;
                color: #F2994A;

                &.Applied {
                    border: 1px solid #006FE3;
                    color: #006FE3;
                }

                &.Watting {
                    border: 1px solid #F2994A;
                    color: #F2994A;
                }

                &.Pass {
                    border: 1px solid #27AE60;
                    color: #27AE60;
                }

                &.Complete {
                    border: 1px solid #27AE60;
                    color: #27AE60;
                }

                &.Fail {
                    border: 1px solid #EB5757;
                    color: #EB5757;
                }
            }
        }
    }
}
</style>
