<template>
<div class="my-page-wrapper container-fluid">
    <div class="row find-tabs">
        <div class="col" :class="page.tab == currentPage ? 'active' : ''" @click="() => gotoPage(page.tab)" v-for="(page, index) in listPages" :key="index"><span class="find-tab"><span>{{$t(`MyPage.${page.name}`)}}</span></span></div>
    </div>
    <div class="row my-page-content" v-if="currentPage == listPages[0].tab">
        <MyCareer />
    </div>
    <div class="row my-page-content" v-if="currentPage == listPages[1].tab">
        <ApplyList />
    </div>
    <div class="row my-page-content" v-if="currentPage == listPages[2].tab">
        <ContactList />
    </div>
</div>
</template>

<script>
import ApplyList from '@/components/MyPage/ApplyList';
import MyCareer from '@/components/MyPage/MyCareer';
import ContactList from '@/components/MyPage/ContactList';

export default {
    name: 'MyPage',
    components: {
        ContactList,
        ApplyList,
        MyCareer
    },
    props: {
        tab: String
    },
    data: function () {
        return {
            listPages: [{
                tab: 'career',
                name: 'MyCareer'
            }, {
                name: 'ApplyStatus',
                tab: 'apply-list'
            }, {
                name: 'ContactList',
                tab: 'contact-list'
            }],
        }
    },
    computed: {
        currentPage: function () {
            return this.$route.params.tab
        }
    },
    methods: {
        gotoPage: function (tab) {
            return this.$router.replace('/my-page/' + tab)
        }
    },
}
</script>

<style lang="scss">
.my-page-wrapper {
    .gap-container {
        background-color: #e5e5e5;
    }
}
</style>
