<template>
<div class="mypage-info-item">
    <div class="container-fluid">
        <div class="row row-info-item">
            <span class="col title">{{$t('LanguageSkills.TITLE')}}</span>
            <span class="col action edit" @click="gotoLangPage"></span>
        </div>
        <div class="divider"></div>
        <div class="row row-info-item body">
            <div class="col-12">
                <div class="row" v-for="(value, label, index) in languages" :key="index">
                    <span class="col keys">{{label}}</span>
                    <span class="col values">{{value}}</span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "langskills-item",
    data: function () {
        return {
            languages: {}
        }
    },
    async created() {
        await this.setData();
    },
    computed: {
        personalInfo() {
            return this.$store.state.MyPage.myCareer;
        },
    },
    methods: {
        gotoLangPage: function () {
            return this.$router.push({
                name: 'LanguageSkillsContainer',
                params: {
                    skills: this.personalInfo
                }
            });
        },
        setData() {
            if (this.personalInfo) {
                this.languages[this.$t('LanguageSkills.KOREAN')] = this.personalInfo.langKr ? this.$t(`LanguageSkills.${this.personalInfo.langKr}`) : this.$t('LanguageSkills.N');
                this.languages[this.$t('LanguageSkills.ENGLISH')] = this.personalInfo.langEn ? this.$t(`LanguageSkills.${this.personalInfo.langEn}`) : this.$t('LanguageSkills.N');
                this.languages[this.$t('LanguageSkills.CHINESE')] = this.personalInfo.langCn ? this.$t(`LanguageSkills.${this.personalInfo.langCn}`) : this.$t('LanguageSkills.N');
                this.languages[this.$t('LanguageSkills.JAPANESE')] = this.personalInfo.langJp ? this.$t(`LanguageSkills.${this.personalInfo.langJp}`) : this.$t('LanguageSkills.N');
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.mypage-info-item {
    display: inline-flex;
    width: 100%;
    background-color: #FFFFFF;

    .row-info-item {
        padding-top: .8rem;
        padding-bottom: 1.0rem;

        &.body {
            padding-top: 0;
            padding-bottom: 1rem;

            .bg {
                background-color: rgba(38, 153, 251, 0.1);
            }
        }
    }
}

.col {
    &.keys {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 30px;
        display: flex;
        align-items: center;
        color: #4F4F4F;
        margin: 4.5px 0;
    }

    &.values {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 30px;
        display: flex;
        align-items: center;
        color: #4F4F4F;
        justify-content: flex-end;
        margin: 4.5px 0;
    }

    &.title {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2.3rem;
        display: flex;
        align-items: center;
        color: #333333;
    }

    &.action {
        max-width: 3.5rem;
        padding-left: 0;
    }

    &.avatar {
        background-image: url(~@/assets/images/avatar-example.png);
        background-repeat: no-repeat;
        background-size: 75px 100px;
        background-position: right;
        height: 100px;
        max-width: 90px;
        padding-right: 0;
    }

    &.info {
        .name {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 23px;
            display: flex;
            align-items: center;
            color: #000000;
            margin-bottom: 6px;
        }

        .detail {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 22px;
            color: #4F4F4F;
            display: inline-flex;
            width: 100%;
        }
    }
}

.action {
    background-repeat: no-repeat;
    background-position: left;
    background-size: 2.0rem 2.0rem;

    &.edit {
        background-image: url(~@/assets/images/edit.png);
    }
}

.divider {
    height: 0.1rem;
    background-color: #E0E0E0;
}

.status-btn {
    background: rgba(38, 153, 251, 0.1);
    border-radius: 100px;
    padding: 0 10px;
    font-family: NotoSansKR;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 36px;
    display: flex;
    align-items: center;
    text-align: right;
    color: #006FE3;
    height: 27px;
}
</style>
