<template>
<div class="view-complete">
    <div class="content" @click="filter">
        <img :src="statusComplete ? circleCheck : circleUnCheck" alt="">
        <span>{{$t('Project.Complete')}}
        </span>
    </div>
</div>
</template>

<script>
import circleCheck from '@/assets/images/circle-check.png';
import circleUnCheck from '@/assets/images/circle-uncheck.png'

export default {
    name: 'ViewComplete',
    components: {},
    data: function () {
        return {
            circleCheck,
            circleUnCheck,
        }
    },
    props: {
        filter: {
            type: Function,
            default: () => {}
        },
        statusComplete: {
            type: Boolean,
            default: false
        }
    },
    mounted: function () {},
}
</script>

<style lang="scss">
.view-complete {
    font-family: NotoSansKR;
    font-size: 1.4rem;
    line-height: 2rem;
    color: #4f4f4f;
    background: #e5e5e5;
    height: 4rem;

    .content {
        position: absolute;
        right: 4rem;
        line-height: 4rem;
    }

