<template>
<div class="w-100">
    <div class="col my-page-view-complete">
        <ViewComplete :statusComplete="statusComplete" :filter="filterComplete" />
    </div>
    <div class="apply" v-if="applies">
        <template v-for="(apply, index) in applies">
            <ContactItem :contact="apply" :key="index" :type="'apply'" />
        </template>
        <InfiniteLoading :isError="isError" :isComplete="isComplete" :fetchData="fetchApply" :classParent="'.apply'" />
    </div>
    <div class="apply" v-if="isUpdateApply && applies && !applies.length">
        <div class="no-results" slot="no-results">{{$t('Common.Loading.NoResult')}}</div>
    </div>
</div>
</template>

<script>
import ContactItem from '@/components/MyPage/ContactItem'
import ViewComplete from '@/components/Common/Button/ViewComplete'
import InfiniteLoading from '@/components/Common/Loading/InfiniteLoading'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_APPLY_LIST,
    CREATE_APPLY_LIST,
    RESET_APPLY_LIST
} from '@/mutations/MyPage.mutation'
import {
    PROJECT_STATUS,
} from '@/constants'

export default {
    name: 'ApplyList',
    components: {
        ContactItem,
        ViewComplete,
        InfiniteLoading
    },
    data: function () {
        return {
            page: 0,
            isError: false,
            isComplete: false,
            statusComplete: false,
            user: JSON.parse(localStorage.getItem('user')),
            isUpdateApply: null
        }
    },
    computed: {
        applies() {
            let apls = this.$store.state.MyPage.applies;
            if (this.statusComplete) {
                return apls.filter(apl => apl.projStatus == PROJECT_STATUS.COMPLETE)
            } 
            return apls;
        },
        totalApply() {
            return this.$store.state.MyPage.totalApply;
        }
    },
    async created() {
        await this.$store.dispatch("CREATE_APPLY_LIST");
    },
    methods: {
        async fetchApply() {
            try {
                this.page += 1;
                let params = {
                    pageNum: this.page + ''
                }

                if (this.user) {
                    params['userId'] = this.user.userId
                }
                await this.$store.dispatch("FETCH_APPLY_LIST", params);
                this.isUpdateApply = true

                if (this.totalApply == 0 || this.applies && this.applies.length == this.totalApply) {
                    this.isComplete = true;
                }
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
                this.isError = true;
            }
        },

        resetData() {
            this.$store.dispatch('RESET_APPLY_LIST')
            this.page = 0;
            this.isComplete = false;
            this.isUpdateApply = false;
            this.isError = false;
        },
        filterComplete() {
            this.statusComplete = !this.statusComplete
        }
    },
    beforeDestroy() {
        this.resetData();
    }
}
</script>

<style lang="scss" scoped>
.apply {
    max-height: calc(100vh - 17rem);
    overflow: auto;
    height: 100%;
}
</style>
